.container {
    padding: 15px;
    background-color: #fff;
    border-radius: 15px;
    margin-bottom: 20px;
    box-shadow: 0 8px 30px rgba(0, 0, 0, .05);
}

.body {
    display: flex;
    padding: 0 20px 20px;
    font-family: Montserrat, sans-serif;
}

.avatar {
    flex: 0 0 110px;
    height: 110px;
    padding: 5px;
    background-color: white;
    border-radius: 50%;
    margin: -55px 0 0 0;
    position: relative;
}

.avatar div {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    overflow: hidden;
    position: relative;
}

.avatar img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.avatar:hover .file {
    opacity: 1;
    visibility: visible;
}

.block {
    padding: 0 0 0 50px;
    color: #333;
}

.background {
    position: relative;
    padding: 0 0 26% 0;
    margin-bottom: 20px;
    border-radius: 15px;
    overflow: hidden;
}

.background img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.fullName {
    font-weight: 700;
    font-size: 22px;
    margin-bottom: 10px;
    color: #111;
}

.file {
    cursor: pointer;
    position: absolute;
    z-index: 2;
    bottom: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s ease 0s;
    font-size: 20px;
    color: #1e74fd;

}

.file input {
    position: absolute;
    bottom: 0;
    right: 0;
    height: 100%;
    width: 100%;
    opacity: 0;
}

.contacts {
    margin-bottom: 5px;
}

.contactItem {
    padding-left: 20px;
    margin-bottom: 5px;
    display: flex;
}

.subtitle {
    font-size: 16px;
    font-weight: 600;
    color: #212529;
    margin-right: 5px;
}

.social {
    font-size: 15px;
    color: #adb5bd;
}

.social:hover {
    text-decoration: underline;
    cursor: pointer;
}

.button {
    background-color: #05f;
    border-radius: 8px;
    color: #fff;
    padding: 5px 20px;
    font-size: 14px;
    transition: background-color 0.5s ease 0s;
}


.button:hover {
    background-color: #04389e;
}

.item {
    font-family: Montserrat, sans-serif;
}

.item:not(:last-child) {
    margin-bottom: 10px;
}

.title {
    font-size: 18px;
    font-weight: 700;
    color: #212529;
    margin-bottom: 5px;
}

.text {
    font-size: 16px;
    font-weight: 500;
    color: #adb5bd;
}

.actions {
    flex: 1 1 auto;
    display: flex;
    justify-content: flex-end;
}

.friend {
    height: 50px;
    background-color: #10d876;
    padding: 5px 17px;
    color: #fff;
    font-size: 12px;
    border-radius: 5px;
    font-weight: 700;
    letter-spacing: 1px;
    text-transform: uppercase;
    margin-right: 8px;
}

.message {
    height: 50px;
    width: 50px;
    border-radius: 5px;
    color: #495057;
    background-color: #f5f5f5;
    font-size: 20px;
}

@media (any-hover: hover) {
    .friend {
        transition: background-color 0.4s ease 0s;
    }

    .friend:hover {
        background-color: rgba(16, 216, 118, 0.7);
    }

    .message {
        transition: background-color 0.3s ease 0s;
    }

    .message:hover {
        background-color: #dddcdc;
    }
}